import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import Banner from '../components/Banner'

import pic01 from '../assets/images/pic01.jpg'
import pic02 from '../assets/images/pic02.jpg'
import pic03 from '../assets/images/pic03.jpg'
import pic04 from '../assets/images/pic04.jpg'
import pic05 from '../assets/images/pic05.jpg'
import pic06 from '../assets/images/pic06.jpg'

class HomeIndex extends React.Component {
    render() {
        return (
            <div>
                <Helmet>
                    <title>Anshul</title>
                    <meta name="description" content="Web Development" />
                </Helmet>

                <Banner />

                <div id="main">
                    <section id="one" className="tiles">
                        <article style={{backgroundImage: `url(${pic01})`}}>
                            <header className="major">
                                <h3>Performance</h3>
                                <p>Respond quickly to user interactions with silky smooth animations and no janky scrolling</p>
                            </header>
                            <Link to="/performance" className="link primary"></Link>
                        </article>
                        <article style={{backgroundImage: `url(${pic02})`}}>
                            <header className="major">
                                <h3>Multi-Device Support </h3>
                                <p> Screen sizes are always changing, so it's important that your site can adapt to any screen size, today or in the future.</p>
                            </header>
                            <Link to="/multi_device" className="link primary"></Link>
                        </article>
                        <article style={{backgroundImage: `url(${pic03})`}}>
                            <header className="major">
                                <h3>Fast Loading Speeds</h3>
                                <p>53% of mobile users abandon sites that take longer than 3 seconds to load.</p>
                             </header>
                            <Link to="/loading_speeds" className="link primary"></Link>
                        </article>
                        <article style={{backgroundImage: `url(${pic04})`}}>
                            <header className="major">
                                <h3>Base Technologies used</h3>
                                <p>ReactJS, Gatsby, Netlify</p>
                            </header>
                            <Link to="/base_technologies" className="link primary"></Link>
                        </article>
                        <article style={{backgroundImage: `url(${pic05})`}}>
                            <header className="major">
                                <h3>Clean Design</h3>
                                <p>Mobile users are very goal-oriented. They expect to be able to get what they need, immediately, and on their own terms.</p>
                            </header>
                            <Link to="/clean_design" className="link primary"></Link>
                        </article>
                        <article style={{backgroundImage: `url(${pic06})`}}>
                            <header className="major">
                                <h3>Product and pricing</h3>
                                <p>Feugiat amet tempus</p>
                            </header>
                            <Link to="/products" className="link primary"></Link>
                        </article>
                    </section>
                    <section id="two">
                        <div className="inner">
                            <header className="major">
                                <h2>Massa libero</h2>
                            </header>
                            <p>Nullam et orci eu lorem consequat tincidunt vivamus et sagittis libero. Mauris aliquet magna magna sed nunc rhoncus pharetra. Pellentesque condimentum sem. In efficitur ligula tate urna. Maecenas laoreet massa vel lacinia pellentesque lorem ipsum dolor. Nullam et orci eu lorem consequat tincidunt. Vivamus et sagittis libero. Mauris aliquet magna magna sed nunc rhoncus amet pharetra et feugiat tempus.</p>
                            <ul className="actions">
                                <li><Link to="/performance" className="button next">Get Started</Link></li>
                            </ul>
                        </div>
                    </section>
                </div>

            </div>
        )
    }
}

export default HomeIndex




